/* Apply "..." after 3 lines of text */
/* .text-style-3lines {
                      display: -webkit-box;
                      overflow: hidden;
                      -webkit-line-clamp: 3;
                      -webkit-box-orient: vertical;
                  } */
.text-style-3lines {
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
}

/* Apply "..." after 2 lines of text */
/* .text-style-2lines {
                      display: -webkit-box;
                      overflow: hidden;
                      -webkit-line-clamp: 2;
                      -webkit-box-orient: vertical;
                  } */
.text-style-2lines {
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
}

/* Adds inline flex display */
.display-inlineflex {
  display: inline-flex;
}

/* These classes are never overwritten */
.hide {
  display: none !important;
}

