/* Apply "..." at 100% width */
.truncate-width {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Removes native scrollbar */
.no-scrollbar {
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;
}

.no-scrollbar::-webkit-scrollbar {
  display: none;
}


@media only screen and (max-width: 600px) {

  .accquisition-card_para br,
  .new-content_para br,
  .hero_heading br,
  .new-content_heading br {
      display: none;
  }
}

.hero_highlight {
  background-size: 200% auto;
  animation: gradient-animation 5s ease-in-out infinite alternate;
}

@keyframes gradient-animation {
  0% {
      background-position: 0% 50%;
  }

  100% {
      background-position: 100% 50%;
  }
}

/* Lighter style for adjacent elements (dropdowns and links) */
.lighter-element {
  opacity: 0.7;
}

/* Navbar active state */
.navbar5_component.active-navbar {
  background: black !important;
}

/* Backdrop display when navbar is active */
.navbar5_component.active-navbar~.navbar_backdrop {
  display: block;
}

/* Dropdown list initial state */
.navbar5_dropdown-list {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-in-out;
}

/* Class to remove transition for quick closing */
.navbar5_dropdown-list.no-transition {
  transition: none;
}

/* Class to hide dropdown instantly */
.navbar5_dropdown-list.hidden {
  display: none;
}

/* Hide the content of the dropdown */
.navbar5_dropdown-list.hide-content .navbar5_dropdown-link {
  display: none;
}

.tab-menu.w--current .tab-menu-text {
  color: black;
  font-weight: 600;
}

.clear-dev>* {
  position: relative !important;
}

body .clear-dev>.layout349_block {
  position: relative !important;
}

